import React from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import BrowseTrials from './BrowseTrials.js';
import BrowseFavourites from './BrowseFavourites.js';
import NotFound from './NotFound.js';

const App = () => {
    return (
        <BrowserRouter basename='/TrialGatePilot'>
            <Switch>
                <Route path="/" component={BrowseTrials} exact />
                <Route path="/TrialGatePilot/favourites" component={BrowseFavourites} exact />
                <Route component={NotFound} />
            </Switch>
        </BrowserRouter>
    )
}

export default App;